@charset "UTF-8";
@keyframes moveInLeft {
  0% {
    opacity: 0;
    transform: translateX(-10rem);
  }
  50% {
    transform: translateX(2rem);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}
:root {
  --color-primary-dark: #0f1934;
  --color-primary-light: #3055ba;
  --color-secondary: #ff4d00;
  --color-secondary-dark: #aa3300;
  --color-secondary-light: #FFA680;
  --color-grey-dark-1: #a9a9a9;
  --color-black: #000;
  --color-white: #fff;
}

* {
  margin: 0;
  padding: 0;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  font-size: 50%;
}

body {
  font-family: "Lato", sans-serif;
  font-weight: 400;
  line-height: 1.5;
  color: var(--color-black);
  position: relative;
  overflow-x: hidden;
  min-height: 1000px;
}

.title-box {
  position: absolute;
  bottom: 0;
  right: 0;
  display: flex;
  flex-direction: column;
  width: 97%;
  direction: rtl;
  background-color: rgba(128, 128, 128, 0.6);
  border-top: 2px solid var(--color-secondary);
  padding: 0.5rem;
}
.title-box__title {
  font-size: 2.5rem;
  font-weight: 500;
  color: var(--color-white);
  letter-spacing: 3px;
  text-transform: uppercase;
}
.title-box__text {
  font-size: 2rem;
  color: var(--color-secondary);
  margin-bottom: 0.5rem;
}

.paragraph {
  font-size: 1.7rem;
  font-weight: 300;
  color: var(--color-white);
}
.paragraph:not(:last-child) {
  margin-bottom: 5rem;
}

