}

.header {
  width: 100%;
  background-image: linear-gradient(to left, var(--color-primary-light), var(--color-primary-dark));
  padding: 1rem 0;
}

.mobile-nav__checkbox {
  display: none;
}
.mobile-nav__button {
  display: flex;
  justify-content: center;
  background-color: var(--color-secondary);
  height: 4.5rem;
  width: 4.5rem;
  position: fixed;
  top: 3rem;
  right: 1rem;
  border-radius: 50%;
  z-index: 2000;
  box-shadow: 0 2rem 2rem rgba(0, 0, 0, 0.1);
  cursor: pointer;
}
.mobile-nav__background {
  height: 3rem;
  width: 3rem;
  border-radius: 50%;
  position: fixed;
  top: 3.5rem;
  right: 1.5rem;
  background-image: radial-gradient(var(--color-primary-dark), var(--color-primary-dark));
  z-index: 1000;
  transition: transform 0.8s cubic-bezier(0.86, 0, 0.07, 1);
}
.mobile-nav__navigation {
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1100;
  opacity: 0;
  width: 0;
  transition: all 0.8s cubic-bezier(0.68, -0.55, 0.265, 1.55);
  visibility: hidden;
}
.mobile-nav__list {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  list-style: none;
}
.mobile-nav__item {
  margin: 1rem;
}
.mobile-nav__link:link, .mobile-nav__link:visited {
  display: inline-block;
  font-size: 2rem;
  font-weight: 300;
  padding: 1rem 2rem;
  color: var(--color-white);
  text-decoration: none;
  text-transform: uppercase;
}
.mobile-nav__link:link span, .mobile-nav__link:visited span {
  color: var(--color-secondary);
  margin-right: 0.9rem;
  display: inline-block;
}
.mobile-nav__checkbox:checked ~ .mobile-nav__background {
  transform: scale(62);
}
.mobile-nav__checkbox:checked ~ .mobile-nav__navigation {
  opacity: 1;
  width: 100%;
  visibility: visible;
}
.mobile-nav__icon {
  position: relative;
  margin-top: 2.1rem;
}
.mobile-nav__icon, .mobile-nav__icon::before, .mobile-nav__icon::after {
  width: 2.5rem;
  height: 2px;
  background-color: var(--color-primary-dark);
  display: inline-block;
}
.mobile-nav__icon::before, .mobile-nav__icon::after {
  content: "";
  position: absolute;
  left: 0;
  transition: all 0.2s;
}
.mobile-nav__icon::before {
  top: -0.8rem;
}
.mobile-nav__icon::after {
  top: 0.8rem;
}
.mobile-nav__checkbox:checked + .mobile-nav__button .mobile-nav__icon {
  background-color: transparent;
}
.mobile-nav__checkbox:checked + .mobile-nav__button .mobile-nav__icon::before {
  top: 0;
  transform: rotate(135deg);
}
.mobile-nav__checkbox:checked + .mobile-nav__button .mobile-nav__icon::after {
  top: 0;
  transform: rotate(-135deg);
}

.logo {
  display: flex;
  justify-content: center;
  align-items: center;
}
.logo__image-box {
  animation: moveInLeft 2s ease-out forwards;
}
.logo__image {
  max-width: 100%;
  max-height: 8rem;
}

.section-intro {
  width: 100%;
}

.intro-slider__swiper-slide {
  display: flex;
  padding-top: 120%;
  background-size: cover;
  background-position: 50% 50%;
  background-repeat: no-repeat;
  background-color: rgba(0, 0, 0, 0.3);
}
.intro-slider__swiper-slide--1 {
  background-image: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)), url(img/evil-west-small.webp);
}
.intro-slider__swiper-slide--2 {
  background-image: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)), url(img/lords-of-the-fallen-small.webp);
}
.intro-slider__swiper-slide--3 {
  background-image: url(img/sniper-ghost-warrior-3-small.webp);
}
.intro-slider__swiper-slide--4 {
  background-image: url(img/sniper-ghost-warrior-contracts-small.webp);
}
.intro-slider__swiper-slide--5 {
  background-image: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)), url(img/shadow-warrior-3-small.webp);
}

.section-professional {
  width: 100%;
  background-image: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)), url(img/professional-section-bg.webp);
  background-size: cover;
  background-position: 50% 50%;
  padding: 5rem 1rem;
}

.about {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color: var(--color-secondary-dark);
  padding: 2rem;
  margin: 0 auto;
}
.about__image-box {
  display: flex;
  justify-content: center;
  margin: 2rem 0 4rem 0;
}
.about__image {
  width: 90%;
  max-height: 30rem;
  border-radius: 50%;
}
.about__content {
  text-align: left;
}
.about__title {
  font-size: 3rem;
  font-weight: 300;
  color: var(--color-secondary-light);
  letter-spacing: 2px;
  text-transform: uppercase;
  text-align: center;
  margin-bottom: 4rem;
}
.about::before {
  content: "“";
  position: absolute;
  top: 34rem;
  left: 0;
  line-height: 1;
  font-size: 20rem;
  color: var(--color-secondary-light);
  font-family: sans-serif;
  overflow: hidden;
}